@import url("https://fonts.googleapis.com/css2?family=Tangerine:wght@400;700&family=Special+Elite&display=swap");

/* General Styles */
body {
  font-family: "Special Elite", cursive; /* Changed font-family */
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
  background-color: #111;
  color: white;
}

/* Navbar */
.navbar {
  position: fixed;
  top: -100px;
  left: 0;
  width: 100%;
  background: rgba(0, 0, 0, 0.8);
  color: white;
  text-align: left;
  z-index: 1000;
  transition: top 0.5s ease-in-out;
}

.navbar.slide-in {
  top: 0;
}

.navbar.slide-out {
  top: -100px;
}

.navbar ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 50px;
  left: 0;
  width: 100%;
  background: rgba(0, 0, 0, 0.9);
  text-align: left;
  visibility: hidden;
  transition: visibility 0.5s ease-in-out, opacity 0.5s ease-in-out;
  opacity: 0;
}

.navbar ul.show {
  visibility: visible;
  opacity: 1;
}

@media screen and (min-width: 769px) {
  .navbar ul {
    position: static;
    display: flex;
    flex-direction: row;
    justify-content: center;
    background: transparent;
    opacity: 1;
    visibility: visible;
  }
}

.navbar a {
  color: white;
  text-decoration: none;
  font-size: 18px;
  padding: 10px 20px;
  display: block;
  font-family: "Special Elite", cursive; /* Changed font-family */
  transition: text-shadow 0.3s ease-in-out; /* Added transition for glow effect */
}

.navbar a:hover {
  cursor: pointer; /* Change cursor on hover */
  text-shadow: 0 0 15px rgba(255, 255, 255, 1); /* Stronger glow effect */
}

/* Mobile Navbar */
.menu-icon {
  display: none;
  cursor: pointer;
  position: absolute;
  top: 15px;
  right: 20px;
  font-size: 24px;
}

@media screen and (max-width: 768px) {
  .menu-icon {
    display: block;
  }
}

/* Parallax Effect */
.parallax-section {
  position: relative;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: white;
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
}

.parallax-section::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.65);
}

.parallax-section > * {
  position: relative;
  z-index: 2;
}

#home {
  background-image: url("profile pic.jpeg");
}

#event-details-section {
  background-image: url("image2.jpeg");
}

#weekend-section {
  background-image: url("image3.jpeg");
}

#registry-section {
  background-image: url("image4.jpeg");
}

#rsvp-section {
  background-image: url("image5.jpeg");
}

/* Hero Section Fonts */
#home h1 {
  font-family: "Tangerine", cursive;
  font-size: 4rem;
  font-weight: 700;
  text-shadow: 4px 4px 6px rgba(0, 0, 0, 0.6);
}

/* Countdown Timer */
.countdown {
  font-family: "Special Elite", cursive;
  font-size: 1rem;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.6);
  margin-top: 10px;
}

/* Sections */
section {
  padding: 80px 20px;
  text-align: center;
  background: rgba(255, 255, 255, 0.1);
  margin: 20px auto;
  border-radius: 10px;
  width: 90%;
  max-width: 800px;
  opacity: 0; /* Initial state for animation */
  transform: translateY(20px); /* Initial state for animation */
  transition: opacity 0.6s ease-out, transform 0.6s ease-out; /* Animation transition */
}

section.in-view {
  opacity: 1; /* Final state for animation */
  transform: translateY(0); /* Final state for animation */
}

h2 {
  margin-bottom: 20px;
  color: #f5f5f5;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
  font-family: "Special Elite", cursive; /* Changed font-family */
}

p {
  color: #e0e0e0;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.6);
}

/* RSVP Button */
button {
  background: #007bff;
  color: white;
  border: none;
  padding: 10px 15px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
}

button:hover {
  background: #0056b3;
}

/* Adjustments for mobile devices */
@media screen and (max-width: 768px) {
  .navbar ul {
    flex-direction: column;
    align-items: flex-start;
  }

  .parallax-section {
    background-attachment: scroll; /* Change from fixed to scroll */
    background-size: cover; /* Ensure the background starts fully */
    background-repeat: repeat-y; /* Repeat the background vertically */
  }

  #home h1 {
    font-size: 2.5rem;
  }

  .countdown {
    font-size: 0.8rem;
  }

  section {
    padding: 40px 10px;
    width: 95%;
  }
}
